%, 66%);\r\n}\r\n\r\ninput[type=\"range\"]:active::-moz-range-thumb {\r\n  background-color: hsl(174, 89%, 33%);\r\n}\r\n\r\ninput[type=\"range\"]:focus::-moz-range-thumb {\r\n  cursor: -moz-grab;\r\n  cursor: grab;\r\n}\r\n// input range hover, active and focus styles end\r\n\r\n// input range styles end\r\n\r\n.pricing-toggle-monthly-yearly {\r\n  display: flex;\r\n  justify-content: flex-end;\r\n  align-items: center;\r\n\r\n  form {\r\n    margin-inline-start: 1rem;\r\n  }\r\n\r\n  em {\r\n    font-style: normal;\r\n    font-family: \"mediumfont\";\r\n    font-size: 0.650rem;\r\n    margin-inline-start: 1rem;\r\n\r\n    ins {\r\n      margin-inline-start: 1rem;\r\n      color: rgb(255, 0, 0);\r\n    }\r\n  }\r\n}\r\n\r\n.switch {\r\n  position: relative;\r\n  display: inline-block;\r\n  width: 3.75rem;\r\n  height: 1.875rem;\r\n  input { \r\n    opacity: 0;\r\n    width: 0;\r\n    height: 0;\r\n  }\r\n\r\n  .slider {\r\n    position: absolute;\r\n    cursor: pointer;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    background-color: #ccc;\r\n    -webkit-transition: .4s;\r\n    transition: .4s;\r\n  }\r\n\r\n  .slider:hover {\r\n    background-color: hsl(174, 77%, 80%);\r\n  }\r\n\r\n  .slider:before {\r\n    position: absolute;\r\n    top: 0.220rem;\r\n    margin-inline-start: 0.100rem;\r\n    content: \"\";\r\n    height: 1.375rem;\r\n    width: 1.375rem;\r\n    left:0.10rem;\r\n    bottom: 0.281rem;\r\n    background-color: $white;\r\n    -webkit-transition: .4s;\r\n    transition: .4s;\r\n  }\r\n\r\n  input:checked + .slider {\r\n    background-color: hsl(174, 86%, 45%);\r\n  }\r\n  \r\n  input:focus + .slider {\r\n    box-shadow: 0 0 1px hsl(174, 77%, 80%);\r\n  }\r\n\r\n  input:checked + .slider:before {\r\n    transform: translateX(30px);\r\n  }\r\n\r\n  .slider.round {\r\n    border-radius: 2.125rem;\r\n  }\r\n  \r\n  .slider.round:before {\r\n    border-radius: 50%;\r\n  }\r\n}\r\n\r\n@media screen and (max-width: 26.6666667rem) {